:host {
  display: flex;
}

.top-panel {
  color: #fff;
  z-index: 1;
  background: #121212;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 32px;
  padding: 5px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.top-panel__header {
  align-items: center;
  display: flex;
}

.top-panel__title {
  vertical-align: middle;
  margin: 0;
  font-size: 2em;
  font-weight: bold;
}

.top-panel__version {
  margin-left: 10px;
  font-weight: bold;
}

.top-panel__links {
  align-items: center;
  margin-right: 10px;
  display: flex;
}

.top-panel__help-link {
  margin-right: 10px;
}
